.form {
    display: flex;
    flex-direction: column;
    padding: 16px;
    border: 1px solid var(--black);
    border-radius: 4px;
    margin-bottom: 20px;
}

.textarea {
    height: 16px * 3 + 24px;
    padding: 12px 16px;
    border: 1px solid rgba(33, 33, 33, 0.2);
    border-radius: 4px;
    font: inherit;
    line-height: 16px;
    letter-spacing: 0.01em;
    resize: none;
    margin-bottom: 12px;
}

.button {
    width: 100%;
    margin-left: auto;
    border-radius: 4px;
    padding: 12px 24px;
    background-color: var(--indigo);
    color: var(--white);
    font-weight: 500;
}

.button:is(:hover, :focus) {
    background-color: var(--indigo-accent);
}

.button:active {
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
}