@import-normalize;

:root {
  /* Colors */
  --indigo: #3f51b5;
  --indigo-accent: #303f9f;
  --red: #f44336;
  --red-accent: #d32f2f;
  --green: #4caf50;
  --black: #2a2a2a;
  --white: #fff;

  /* Other */
  --primary-font-stack: 'Helvetica Neue', -apple-system, BlinkMacSystemFont,
    'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', sans-serif;
  --timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  margin: 0;
  background-color: var(--white);
  color: var(--black);
  line-height: 1.5;
  font-family: var(--primary-font-stack);
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
}

p {
  margin-top: 0;
}

ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}

button {
  border: none;
  font: inherit;
  cursor: pointer;
  outline: none;
}